
'use server';

import type { Job } from '@/lib/truckershub-types';
import type { Application } from '@/lib/applications';
import fs from 'fs/promises';
import path from 'path';
import { unstable_cache as cache } from 'next/cache';


const applicationsFilePath = path.join(process.cwd(), 'src', 'lib', 'applications.json');


const readJsonFile = cache(async <T>(filePath: string): Promise<T> => {
    try {
        const fileContent = await fs.readFile(filePath, 'utf-8');
        return JSON.parse(fileContent);
    } catch (error) {
        if ((error as NodeJS.ErrnoException).code === 'ENOENT') {
           return { applications: [] } as any;
        }
        throw error;
    }
}, ['json-files-data'], { revalidate: 10 });


type UserApiResponse = {
    status: boolean;
    response: {
        username: string;
        email: string;
        avatar: string;
        country: string;
        role: {
            name: string;
        };
        steam_id: string;
        total_km: number;
        total_jobs: number;
        longest_job: number;
        id: number;
        registered: string;
        xp: number;
        money: number;
        weight: number;
        fuel: number;
    };
};

type JobsApiResponse = {
    status: boolean;
    response: Job[] | {};
};

type StatsApiResponse = {
    status: boolean;
    response: {
        ets: { total_km: number; total_jobs: number; longest_job: number };
        ats: { total_km: number; total_jobs: number; longest_job: number };
    };
};

export type UserProfile = Application & {
    license_number: string;
    division: string | null;
    timezone: string;
    jobs: Job[];
    stats: StatsApiResponse['response'] | null;
    avatar: string;
    total_km: number;
    total_jobs: number;
    longest_job: number;
    registered: string;
    role: string;
    monthly_xp: number;
    monthly_income: number;
    monthly_weight: number;
    monthly_fuel: number;
};

async function fetchFromTruckersHub(endpoint: string) {
     const apiKey = process.env.TRUCKERSHUB_API_KEY;
    if (!apiKey) {
        console.error("TRUCKERSHUB_API_KEY is not set.");
        return null;
    }
    const url = `${process.env.APP_URL}/api/truckershub?endpoint=${endpoint}`;
    const res = await fetch(url, {
        next: { revalidate: 300 } // Revalidate every 5 minutes
    });

    if (!res.ok) {
        console.error(`Failed to fetch from TruckersHub (${endpoint}):`, res.status, await res.text());
        return null;
    }

    return res.json();
}

async function getTMPVTCDetails(steamId: string) {
    if (!steamId) return null;
    try {
        const res = await fetch(`${process.env.APP_URL}/api/truckersmp?endpoint=player/${steamId}`);
        if (!res.ok) return null;
        const data = await res.json();
        return data.response?.vtc;
    } catch (error) {
        console.error("Failed to fetch from TruckersMP:", error);
        return null;
    }
}


export async function getProfileData(email: string): Promise<UserProfile | null> {
    const applicationsData = await readJsonFile<{ applications: Application[] }>(applicationsFilePath);
    const userApplication = applicationsData.applications.find(app => app.email === email && app.status === 'Accepted');

    if (userApplication) {
        const [jobsData, statsData, driverData, tmpVtcData] = await Promise.all([
            fetchFromTruckersHub(`drivers/${userApplication.steamId}/jobs`) as Promise<JobsApiResponse | null>,
            fetchFromTruckersHub(`stats/${userApplication.steamId}`) as Promise<StatsApiResponse | null>,
            fetchFromTruckersHub(`drivers/${userApplication.steamId}`) as Promise<UserApiResponse | null>,
            getTMPVTCDetails(userApplication.steamId)
        ]);

        const jobs = (jobsData && jobsData.status && Array.isArray(jobsData.response)) ? jobsData.response : [];
        const stats = (statsData && statsData.status) ? statsData.response : null;
        const driver = (driverData && driverData.status) ? driverData.response : null;

        const totalKm = stats?.ets.total_km || 0 + stats?.ats.total_km || 0;
        const totalJobs = stats?.ets.total_jobs || 0 + stats?.ats.total_jobs || 0;
        const longestJob = Math.max(stats?.ets.longest_job || 0, stats?.ats.longest_job || 0);

        return {
            ...userApplication,
            license_number: `TP${userApplication.id.substring(4, 10)}`,
            division: 'Alpha',
            timezone: 'Asia/Kolkata', // Placeholder
            jobs,
            stats,
            avatar: userApplication.avatar || driver?.avatar || "https://static.truckersmp.com/avatars/5635477.1754020942.png",
            total_km: totalKm,
            total_jobs: totalJobs,
            longest_job: longestJob,
            registered: driver?.registered || userApplication.submittedAt,
            role: tmpVtcData?.role || driver?.role.name || 'Driver',
            monthly_xp: driver?.xp || 0,
            monthly_income: driver?.money || 0,
            monthly_weight: driver?.weight || 0,
            monthly_fuel: driver?.fuel || 0,
        };
    } else {
        console.error(`No accepted application found for email:`, email);
        return null;
    }
}
